html,
body {
    width: 100%;
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    background: #040404;
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    overflow-x: hidden;
}

h4 {
    font-weight: 100;
}

.btn-toggle {
    background: blue;
    color: white;
    padding: 10px;
    border: 0;
    border-radius: 5px;
    text-transform: uppercase;
    margin: 5px 0;
    outline: none;
    cursor: pointer;

    &:hover,
    &:active {
        background: darken(blue, 5%);
    }
}

.examples {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;

    .example {
        width: 320px;
        height: auto;
        margin: 0 10px 10px 0;
        flex: 0 0 320px;
    }
}

.glsl-canvas {
    width: 320px;
    height: 172px;
    border: 4px solid #333;
    border-radius: 5px;
    overflow: hidden;
    box-sizing: border-box;
    user-select: none;

    &[controls] {
        &.animated {
            cursor: pointer;
            transition: opacity 300ms cubic-bezier(0.4, 0.0, 0.6, 1.0);
            // display: inline-block;
            // position: relative;

            &.paused {
                opacity: 0.5;

                &:hover {
                    opacity: 0.75;
                }
            }

            &.playing {
                border-color: blue;
            }
        }
    }

}

video {
    width: 320px;
    height: 172px;
    border: 4px solid #333;
    border-radius: 5px;
    overflow: hidden;
    box-sizing: border-box;
    object-fit: cover;
}

* {
    user-select: none;
}